const initialState = {
  cart: {},
  items: [],
  note: '',
  cookieName: 'hausLegalAge',
};

const cartReducer = (state, action) => {
  return { ...state, ...action.payload };
};

const saveCartNote = (note) => {
  cart
    .updateNote(note)
    .then((cart) => emitter.emit(eventTypes.cartUpdate, cart));
};

const addItemToCart = (id, props = {}) => {
  return cart
    .addItem(id, props)
    .then((item) => emitter.emit(eventTypes.cartUpdate, item))
}

const EmptyCart = () => (
  <div className="tac cart-empty">
    <ProductCarousel />
    
    <div className="supports-no-cookies">
      <p>Enable cookies to use the shopping cart</p>
    </div>
  </div>
);

const RemoveBundle = (props) => {
  const { setLoading } = props.actions;
  const productsToRemove = props.bundle_products.reduce((result, item) => {
    var key = item.key;
    result[key] = 0;
    return result;
  }, {});

  return (
    <span
      className="cart-block__remove button-text pointer text-faded-black -mb-10"
      onClick={() => {
        setLoading(true);
        removeLineItems(productsToRemove);
      }}
    >
      Remove
    </span>
  );
};

const RemoveProduct = (props) => {
  return (
    <span
      className="cart-block__remove button-text pointer text-faded-black -mb-10"
      onClick={() => {
        if (props.actions) {
          props.actions.setLoading(true);  
        }
        
        removeCartItem(props);
        if (props.properties.hasOwnProperty('gwp')) {
          Cookies.set('hausGWPRemove', true)
        }
      }}
    >
      Remove
    </span>
  );
};

const BundleProductsList = ({ bundle_products }) => {
  return (
    <p className="cart-block__subtitle text-faded-black -no-my -pr-10 italic">
      {bundle_products
        .map((item) => {
          // This is here in case Accentuate randomly changes 
          // the field to an Array.
          // Prevents the cart from breaking. 
          const title = Array.isArray(item.properties._display_title) ? item.properties._display_title[0] : item.properties._display_title;
          return `${title
            .replace('200ml', '')
            .trim()} (${item.quantity})`;
        })
        .join(' ')}
    </p>
  );
};

