const ProductCarousel = (props) => {
  let availableProducts = window.__HAUS.shop;
  if (availableProducts.length === 0) return null; 
  
  const carousel = useRef(null);
  const pressCarousel = useRef(null)
  let flick = null;
  let pressCar = null
  
  useEffect(() => {
    if (availableProducts.length === 0) return false; 

    if (carousel.current) {
      flick = new Flickity(carousel.current, {
        imagesLoaded: true,
        pageDots: false,
        cellAlign: 'center',
        initialIndex: availableProducts.length >= 2 ? 1 : 0,
        arrowShape: 'M14.006 43.525h76.83v7.272H13.904L51.47 88.363l-5.143 5.143-42.709-42.71-.056.006v-.062L.034 47.212l3.528-3.528v-.164h.164L46.317.929 51.46 6.07 14.006 43.525Z'
      });
      
    }  
  })
  
    
  const reviewStarsCount = (count) => {
    const STAR_WIDTH = 15;
    return Math.max(0, (Math.min(5, parseFloat(count)))) * STAR_WIDTH;
  }

  
  return (
    <div>
      <div className="-pt-40 -pb-60">
        Your cart looks a bit empty. Check out our award winners.
      </div>
      
      <div className="no-product-carousel -mt-20">
        <div ref={carousel}>
          {availableProducts.map((obj) => {
            const product = obj.product
            const reviews = obj.reviews;
            const press = obj.featured_press;
            const notes = product.tags.find(tag => tag.includes('haus::notes =>'));
            
            return (
              <div className="carousel-slide" key={`carousel-${product.id}`}>

                <div className="-mb-20" key={`press-${obj.product.id}`}>
                  <p className="-mb-20">{press.quote}</p>
                
                  {press.logo && 
                    <div>
                      <img src={press.logo[0].src} alt={press.quote} />
                    </div>
                  }
                </div>

                <div className="cart-carousel-card product-card w-100" key={`carousel-${product.id}`}>
                  <img src={product.featured_image} alt={product.title} />
                  <div className="product-card__body tac">
                    <h3 className="product-card__title -s-heading">{ product.title }</h3>
                    {notes && 
                      <span className="pill button-text">{notes.replace('haus::notes =>', '').trim()}</span>
                    }
                    
                    {obj.okendo.hasOwnProperty('summaryData') &&
                      <div className="-d-flex -align-center -justify-center -mb-10">
                        <span className="review-stars">
                          <span style={{
                            width: reviewStarsCount(obj.okendo.summaryData.reviewAverageValue)
                          }}></span>
                        </span>
                        <span className='-xs-body -d-inline-block -ml-5'>{obj.okendo.summaryData.reviewAverageValue} stars</span>
                      </div>
                    }
                  </div>
                  <a className="text-link" href={`/products/${product.handle}`}>Shop now</a>
                </div>
              </div>
          )})}
        </div>
      </div>
    </div>
  )
};

