const AddGiftBoxBtn = (props) => {
  
  const [ giftBoxItem, setGiftBoxItem ] = useState()
  const { cart, setLoading, varId, isGiftBoxInCart, setIsGiftBoxInCart } = props;

  useEffect(() => {
    if (cart.items.some(item => item.id === varId)) {
      setIsGiftBoxInCart(true)
      setGiftBoxItem(cart.items.find(item => item.handle.includes('gift-box')))
    } else {
      setIsGiftBoxInCart(false)
    }
  }, [cart.items]);

  return(
    <div className="gift-box__btn-wrap flex -f-nowrap -justify-between -align-center -mt-auto -mb-5 ">
      <span 
        className={`gift-box__btn button-text pointer x-small -mr-auto ${!isGiftBoxInCart ? 'pointer button-text--underline ' : 'button-text--disabled'}`}
        onClick={() => {
          if (isGiftBoxInCart) return;
          setLoading(true);
          addItemToCart(varId);
        }}
        >
          { isGiftBoxInCart ? "Added" : "Add to Bag" }
          { isGiftBoxInCart && (
            <svg className="gift-box__added-icon -ml-5" viewBox="0 0 12 9" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M10.6777 0L10.0166 0.666992C8.1941 2.49341 6.24965 4.56651 4.42906 6.41834L1.91321 4.34716L1.18771 3.75037L0 5.19551L0.719649 5.7923L3.90249 8.41346L4.56363 8.95758L5.16041 8.35495C7.17665 6.33439 9.36095 3.97147 11.3389 1.98927L12 1.32228L10.6777 0Z" fill="#696969"/>                   
            </svg>
          )}
          
      </span>
      {!!giftBoxItem && isGiftBoxInCart && (
        <RemoveProduct {...giftBoxItem}> Remove</RemoveProduct>
      )}
    </div>
  )
}

