const GiftNote = (props) => {
  const { cart, setLoading, toggle, setToggle, giftNote, setGiftNote, saved, setSaved, closeOnSave, setIsGiftPanelOpen } = props;
  const emojiRegex = regexCreator();
  
  useEffect(
    () => {
      setGiftNote(cart.note ? cart.note : '');
    },
    [cart.note]
  );

  const countCharactersLeft = (e) => {
    const text = e.target.value.replace(emojiRegex, '');
    
    setGiftNote(text);
    setSaved(false);
  };

  const characterLimit = () => {
    if (!giftNote) return 200;
    return 200 - giftNote.length;
  };


  const clearMessage = () => {
    setGiftNote('');
    saveCartNote('');
    setSaved(false)
  }

  const handleSaveNote = () =>  {
    if (!giftNote.length) return;
    setLoading(true);
    saveCartNote(giftNote.replace(emojiRegex, ''));
    setSaved(!saved)
    if (closeOnSave) {
      setIsGiftPanelOpen(false);
      setToggle(false);
    }
  }

  return (
    <div className="cart-view__messaging oh  -pb-20 bps-pb-30 b-bottom-french" style={{ display: toggle ? "block" : "none" }}>
      <h2 className="note-header -mt-0 -mb-30">Add a complimentary note</h2>
      <p className="small -no-mt -mb-30">We’ll include a personalized note with your order. No emojis, please! (Gift notes are unavailable for gift cards and orders shipped via Ohi delivery.)</p>
      <div>
        <textarea onChange={countCharactersLeft} className="custom-textarea -py-5 -px-10" name="note" id="CartSpecialInstructions" maxLength="200" placeholder="Type your message here..." value={giftNote} />
        <div className="flex -my-20">
          <span className="gta-expanded tiny lts-01 text-faded-black tac ttu -d-block">
            {characterLimit()} characters remaining
          </span>
          <span className={`button-text base-text -ml-auto clear-note-btn ${giftNote.length ? 'pointer' : 'button-text--disabled'}`} onClick={giftNote.length ? clearMessage : null}> Clear</span>
        </div>

        <div className='save-note-wrap'>
          <button className={`-mt-sm btn btn--full -d-table -mt-10 -mx-auto ${!giftNote.length || saved ? 'is-disabled is-gray' : ''}`} disabled={!giftNote.length} onClick={handleSaveNote}>
              {!saved ? 'Save Message' : 'Saved Message'}
          </button>
        </div>
      </div>
    </div>
  );
};

