const BackOrderNotice = (props) => {
  const [tags, setTags] = useState([]);
  const [shipTag, setShipTag] = useState(false);
  const shipTagNeedle = 'haus::shipdate';
  let hasTag = false;

  useEffect(() => {
    getProductTagsByHandle(props.handle, setTags);
  }, []);

  useEffect(
    () => {
      getShipDate(tags);
    },
    [tags]
  );

  const getShipDate = (tags) => {
    const found = tags.find((tag) => {
      return tag.includes(shipTagNeedle);
    });

    if (found) {
      setShipTag(found.split('=>')[1].trim());
    }
  };

  if (!shipTag) return null;

  return (
    <p className="cart-block__subtitle text-faded-black -no-my italic">
      Ships By {shipTag}
    </p>
  );
};

const CartMinimumUpsell = (props) => {
  let availableProducts = window.__HAUS.shop;
  const {
    canCheckout,
    items
  } = props
  
  if (canCheckout) return null;
  
  if (availableProducts.length === 0) return null;
  
  const hasItemsFromCart = items.filter(item => availableProducts.some(obj => obj.product.handle === item.handle));
  
  availableProducts = availableProducts.filter(obj => {
    return !hasItemsFromCart.some(item => item.handle === obj.product.handle);
  })
  
  if (availableProducts.length > 0) {
    availableProducts = availableProducts.slice(0, 2);
  }
  
  return (
    <div className="-mt-20">
      <p className="eyebrow -mb-15">Need another bottle?</p>
      {availableProducts.map((obj) => {
        const product = obj.product
        const notes = product.tags.find(tag => tag.includes('haus::notes =>'));
        
        return (
        <div className="row -d-flex -py-10 b-bottom-travis-gray" key={`min-upsell-${product.id}`}>
          <div className="col -d-flex -align-between">
            <div className="-pr-10 cart-block__media upsell-block cart-mini-block">
              <a className="-d-block"  href={`/products/${product.handle}`}>
                <img className="-d-block" src={product.featured_image} alt={product.media[0].alt} />
              </a>
            </div>
            
            <div className="col">
              <div className="-d-flex -f-col -justify-between -align-start h-100">
                <p className="-no-my -pr-10 -d-flex -justify-between w-100 cart-product-title">{product.title} <span>{formatMoney(product.price, moneyFormats.noDecimals)}</span></p>
                
                <div className='-mb-10'>
                  {notes && 
                    <span className="-xxs-body -d-block">{notes.replace('haus::notes =>', '').trim()}</span>
                  }
                </div>
                
                <button className="-shrink-0 text-link -no-pl -mt-auto -mb-15" onClick={() => addItemToCart(product.variants[0].id)}>Add To Cart</button>
              </div>
            </div>
          </div>
        </div>
      )})}
    </div>
  )
}

