const Loading = () => {
  return <div className="cart-view__loader" />;
};

const AgeConsent = ({ state, consentHandler, modalHandler }) => {
  const { cookieName } = initialState;

  return (
    <div className="age-consent gutter-px-xs-3 gutter-px-sm-2 -pt-40 -pb-55 bps-pt-55 bps-pb-70">
      <h3 className="-s-heading -no-mt">Are you 21 or older?</h3>
      <p className="-caption bps-body -no-my">
        You must be of legal drinking age to purchase any Haus products.
      </p>
      <div className="-mt-30">
        <button
          className="btn btn--white -mx-5 -mb-10"
          onClick={() => {
            Cookies.set(cookieName, 'true');
            consentHandler(true);
            modalHandler(false);
            goToCheckout(state.cart);
          }}
        >
          Yes
        </button>
        <button
          className="btn btn--white -mx-5"
          onClick={() => {
            Cookies.set(cookieName, 'false');
            consentHandler(false);
            modalHandler(false);
            redirectForUnderageCustomers();
          }}
        >
          No
        </button>
      </div>
    </div>
  );
};

const redirectForUnderageCustomers = () =>
  cart
    .clearItems()
    .then(() => (window.location = '/pages/age-gate-newsletter'));

const MiniCart = () => {
  const [state, dispatch] = useReducer(cartReducer, initialState);
  const [loading, setLoading] = useState(true);
  const [isGiftPanelOpen, setIsGiftPanelOpen] = useState(false);
  const cartBody = useRef(null);
  const cartFooter = useRef(null);
  const [cartGWP, setCartGWP] = useState(false);
  const [canCheckout, setcanCheckout] = useState(false);
  
  // For Age Gate
  const [openModal, setModalOpen] = useState(false);
  const [isLegal, setIsLegal] = useState(false);
  const [hasCookie, setCookie] = useState(false);
  const [cartOffsetHeight, setCartOffsetHeight] = useState(false)
  const gwpProduct = window.__HAUS.gwp.product;
  const gwpThreshold = window.__HAUS.gwp.threshold
  const insertProduct = window.__HAUS.insert.product;

  const findCurrentBundle = (arrayToSearch, id) => {
    return arrayToSearch.find((bundle) => bundle.id === id);
  };

  const miniCartHeader = document.querySelector('[data-cart-header-wrap]');
  const root = document.getElementById('mini-cart');

  const handleCheckout = (e) => {
    const { cookieName } = initialState;
    e.preventDefault();
    goToCheckout(state.cart);
  };

  const sortCartItems = (items) => {
    const giftBox = [];
    const bundles = [];
    const cartItems = [];

    items.map((item, i) => {
      const { product_title, properties } = item;
      
      item.haus_lineItem = i + 1;
      item.lineItemKey = item.key;
      
      const isGiftBox = product_title.toLowerCase().includes('gift box');
      
      const currentBundleID = properties && properties.hasOwnProperty('_bundle_id')
        ? properties._bundle_id
        : false;
      const currentBundle = currentBundleID
        ? findCurrentBundle(bundles, currentBundleID)
        : false;
      if (currentBundleID) {
        if (!currentBundle) {
          bundles.push({
            blockType: 'bundle',
            featured_image: properties._bundle_image,
            id: currentBundleID,
            product_title: properties._bundle_product,
            price: properties._bundle_price,
            bundle_products: [item],
            handle: properties._bundle_handle
          });
        } else {
          currentBundle.bundle_products.push(item);
        }
      } else if (isGiftBox) {
        giftBox.push(item)
      } else {
        if (!item.properties || (item.properties && !item.properties.hasOwnProperty('_hideFromCart'))) {
          item.blockType = 'product';
          cartItems.push(item);
        }
      }
    });
    
    return [].concat(giftBox, bundles, cartItems);
    
  };
  
  const checkForAutoInsert = () => {
    return cart.getState().then(cart => {
      const countProp = '_bundle_count'
      const applicableCount = 4
      const {
        items
      } = cart;
      
      if (items.length === 0) return false;
      
      // Check to see if we have the sampler kit
      // and if sampler kit is a 4 pack
      // at time of writing (10/14/21) we only need to add 
      // the editorial insert into 4pk items. 
      // this may change in the future thus logic may need to be abstracted.
      const hasSampler = items.findIndex(product => {
        return product.properties && product.properties.hasOwnProperty(countProp) && product.properties[countProp] === applicableCount;
      });
      
      const hasInsert = items.findIndex(product => product.handle === insertProduct.handle);
      
      // Cart has one item and its the sampler kit
      // OR 
      // cart doesn't have sampler and it does have insert
      // We remove insert
      if ((items.length === 1 && hasInsert > -1) || (hasSampler === -1 && hasInsert > -1)) {
        items[hasInsert].lineItemKey = items[hasInsert].key
        removeCartItem(items[hasInsert])
      }
      
      
      // Has sampler
      // AND 
      // Does not have Insert
      if (hasSampler !== -1 && hasInsert === -1) {
        addItemToCart(insertProduct.variants[0].id, {
          properties: {
            '_hideFromCart': true
          }
        })
      }
      return true;
    })
  }
  
  const checkForGWP = () => {
    let gwpCount = 0;
    
    return cart.getState().then(cart => {
      const {
        items
      } = cart;
      
      if (gwpProduct && !Cookies.get('hausGWPAdded')) {
        const gwpAlreadyInCart = items.filter(product => product.handle === gwpProduct.handle);
        const cartAlreadyHasGWP = items.filter((item) => item.properties.hasOwnProperty('gwp'))
        
        items.map((item) => {
            const { product_title, properties,  product_type, quantity } = item;
            
            if (!product_title.toLowerCase().includes('200ml') && product_type.toLowerCase() === 'wine') {
              gwpCount = gwpCount + quantity;  
            }
        })
        
        const shouldAddToCart = gwpAlreadyInCart.length === 0 && cartAlreadyHasGWP.length === 0 && gwpCount >= gwpThreshold && !Cookies.get('hausGWPRemove')
        
        if (shouldAddToCart) {      
          addItemToCart(gwpProduct.variants[0].id, {
            properties: {
              'gwp': true
            }
          }).then(() => {
            Cookies.set('hausGWPAdded', true)
          }) 
        } else {
          return false;
        }
      }
      
      return true;
    })
  }
  
  const checkIfUserCanCheckout = (cart) => {
    const {
      items,
      item_count
    } = cart;
    
    const foundGroupedProducts = []
    const foundSubscriptionProducts = [];
    items.map(product => {
      const productTitle = product.product_title.toLowerCase();
      const kitString = 'kit';
      const twoBottle = '2 bottle membership'
      const sixBottle = '6 bottle membership'
      const giftCard = 'gift card'
      
      if (productTitle.includes(kitString)) {
        foundGroupedProducts.push(product)
      }
      
      if (productTitle.includes(twoBottle) || productTitle.includes(sixBottle)) {
        foundSubscriptionProducts.push(product)
      }
      
      if (product.product_type.toLowerCase() === giftCard) {
        foundGroupedProducts.push(product);
      }
    })
    
    return  item_count >= 2 || items.length > 2 || foundGroupedProducts.length !== 0 || foundSubscriptionProducts.length !== 0
  }

  const hydrateCart = async () => {
    if (insertProduct) {
      const insertCheck = await checkForAutoInsert();  
    }
    
    if (gwpProduct) {
      const gwpCheck = await checkForGWP();  
    }
    
    
    return cart.getState().then((cart) => {
      dispatch({
        type: 'cart',
        payload: {
          cart,
          items: sortCartItems(cart.items),
        },
      });
      
      emitter.emit(eventTypes.cartCountUpdate, {cart});
      setcanCheckout(checkIfUserCanCheckout(cart));
      setLoading(false);
    });
  };

  useEffect(() => {
    // runs on initial load
    hydrateCart();
    
    emitter.on(eventTypes.cartUpdate, (cart) => {
      hydrateCart();
      return cart
    })
  }, []);

  useEffect(
    () => {
      if (cartFooter.current && miniCartHeader) {
        const offsetHeight =
          cartFooter.current.scrollHeight + miniCartHeader.scrollHeight;
        root.style.height = `calc(100% - ${offsetHeight}px)`;
        setCartOffsetHeight(offsetHeight);
      }

      if (isGiftPanelOpen) {
        root.style.overflow = 'hidden';
      } else {
        root.style.overflow = 'auto';
      }
    },
    
    [state, isGiftPanelOpen]
  );

  return (
    <React.Fragment>
      <div className="cart-body gutter-px-xs-1 gutter-px-lg-2" ref={cartBody}>
        <Modal
          open={openModal}
          classNames="modal--small bg-black text-white tac age-consent"
          showCloseButton={false}
        >
          <AgeConsent
            consentHandler={setIsLegal}
            modalHandler={setModalOpen}
            state={state}
          />
        </Modal>
        {Object.entries(state.items).length === 0 && <EmptyCart />}
        {Object.entries(state.items).length > 0 && (
          <React.Fragment>
            {loading && <Loading />}
            {state.items.map((item, i) => {
              item.actions = {
                hydrate: hydrateCart,
                setLoading: setLoading,
              };

              if (item.blockType === 'bundle') {
                return (
                  <React.Fragment key={i}>
                    <BundleBlock {...item} />
                  </React.Fragment>
                );
              } else {
                return (
                  <React.Fragment key={i}>
                    <ProductBlock {...item} />
                  </React.Fragment>
                );
              }
            })}
            <CartMinimumUpsell canCheckout={canCheckout} {...state} />
            <CartSubtotal {...state} />
          </React.Fragment>
        )}
      </div>
      <React.Fragment>
        {Object.entries(state.items).length > 0 && (
          <MiniCartFooter
            {...state}
            canCheckout={canCheckout}
            setLoading={setLoading}
            handleCheckout={handleCheckout}
            dispatch={dispatch}
            refEl={cartFooter}
            isLegal={isLegal}
            state={state}
            setIsGiftPanelOpen={setIsGiftPanelOpen}
            isGiftPanelOpen={isGiftPanelOpen}
          />
        )}
      </React.Fragment>
    </React.Fragment>
  );
};

export default MiniCart;
