import React, {useEffect, useReducer, useState} from 'react';
import ee, { eventTypes } from '../../scripts/modules/events';

import { addCartItem } from '../actions/cart'
import cx from 'classnames'
import { formatMoney } from '@shopify/theme-currency';
import isEqual from 'lodash.isequal';
import { moneyFormats } from '../../scripts/modules/helpers/buttons';

const ProductOptions = () => {
  const [selected, setSelected] = useState({});
  const [product, setProduct] = useState({});
  const [options, setOptions] = useState([]);
  const [variants, setVariants] = useState([]);
  const [selectedVariant, setSelectedVariant] = useState(false);

  function findVariantByOptions(userOptions) {
    return variants.find(variant => isEqual(variant.options, Object.values(userOptions)));
  }
  
  const displayActiveFeaturedImage = (variant) => {
    const images = [...document.querySelectorAll(`.product-image-wrap`)];

    images.map(image => {
      if (Number(image.dataset.imageId) === variant.featured_image.id) {
        image.classList.remove('-d-none');
        image.classList.add('-d-block')
      } else {
        image.classList.remove('-d-block');
        image.classList.add('-d-none')
      }
    })
  }
  
  
  useEffect(() => {
    /**
     * Set product and option values from the window object. 
     * Wnidow object is how we set the values from Liquid and pass into React
     */
    setProduct(window.__HAUS.product);
    setVariants(window.__HAUS.product.variants);
    setOptions(window.__HAUS.productOptions);
  }, []);
  
  /**
   * Called once when we set options
   * to prepare the initial variant.
   */
  useEffect(() => {
    const output = {};
    const values = window.__HAUS.product.variants.find(variant => variant.available);

    options.map((option) => {
      option.values.map(value => {
        if (values.options.indexOf(value) !== -1) {
          output[option.name.toLowerCase()] = value;
        }
      })
    })
    
    if (Object.keys(output).length) {
      const found = findVariantByOptions(output);
      setSelected(output);
      setSelectedVariant(found);
    }
  }, [options])
  
  useEffect(() => {
    if (Object.keys(selected).length) {
      const found = findVariantByOptions(selected);
      setSelectedVariant(found)  
      
      displayActiveFeaturedImage(found);
      
    } else {
      setSelectedVariant({})  
    }
  }, [selected])
  
  
  function setOption(option, value) {
    setSelected((prevState) => {
      return {...prevState, [option.toLowerCase()]: value};
    });
  }
  
  function addToCart(e) {
    e.preventDefault()
    if (!selectedVariant) return false;
    
    addCartItem({
      id: selectedVariant.id,
      quantity: 1,
    }).then(data => {
      ee.emit(eventTypes.cartUpdate);
    })
  }
  
  function optionIsActive(option, value) {
    const lower = option.toLowerCase();
    return selected[lower] === value
  }
  
  function checkIfProductIsAvailable(product) {
    if (product && Object.keys(product).length) {
      return !product.available;
    } else {
      return true;
    }
  }
  
  function renderVariantPrice(product) {
    if (product) {
      return `Add to cart - ${formatMoney(
        product.price,
        moneyFormats.noDecimals
      )}`
    } else {
      return 'Sold Out';
    }
  }

  return (
    <div>
      {options && options.map((option, idx) => {
        return (
        <div className="-mb-35">
          <p className="label -mb-15" key={option.name}>{option.name}</p>
          <div className=" -d-flex -justify-center bpm-justify-start">
            {option && option.values.map(value => {
              return (
                <span 
                  className={cx(
                    "variant-selector variant-selector--square gta-extended-med ttu",
                    {
                      'is-active': optionIsActive(option.name, value),
                    }
                  )}
                  key={value} 
                  onClick={() => setOption(option.name, value)}>
                    {value}
                </span>
              )
            })}  
          </div>
        </div>)
      })}
      
      <a href="#"  className={cx("btn w-100",  {
        "is-disabled": checkIfProductIsAvailable(selectedVariant),
      })} onClick={addToCart} data-btn-type="standard">{renderVariantPrice(selectedVariant)}</a>  
    </div>
  );
};


export default ProductOptions;
