import React, { useEffect, useReducer } from 'react';
import emitter, { eventTypes } from '../../scripts/modules/events';

import { cartReducer } from '../actions/cart';

const initialState = {
  cart: {},
  items: [],
  count: 0,
};

const BagCounter = () => {
  const [state, dispatch] = useReducer(cartReducer, initialState);
  const findCurrentBundle = (arrayToSearch, id) => {
    return arrayToSearch.find((bundle) => bundle.id === id);
  };

  const generateBagCountFromCart = (items) => {
    const bundles = [];
    const cartItems = [];
    let bagCount = 0;

    items.map((item, i) => {
      const { properties } = item;
      item.haus_lineItem = i + 1;
      item.lineItemKey = item.key;

      const currentBundleID = properties && properties.hasOwnProperty('_bundle_id')
        ? properties._bundle_id
        : false;
        
      const currentBundle = currentBundleID
        ? findCurrentBundle(bundles, currentBundleID)
        : false;
      if (currentBundleID) {
        if (!currentBundle) {
          bundles.push({
            blockType: 'bundle',
            featured_image: item.featured_image,
            id: currentBundleID,
            product_title: properties._bundle_product,
            price: properties._bundle_price,
            bundle_products: [item],
            handle: properties._bundle_handle
          });
        } else {
          currentBundle.bundle_products.push(item);
        }
      } else {
        if (!item.properties || (item.properties && !item.properties.hasOwnProperty('_hideFromCart'))) {
          item.blockType = 'product';
          cartItems.push(item);
        }
      }
    });
    
    cartItems.map(item => bagCount = bagCount + item.quantity)
    bagCount = bagCount + bundles.length;
    
    return bagCount;
  };

  const hydrateCart = async (cart) => {
    const count = await generateBagCountFromCart(cart.items);
    
    return dispatch({
      type: 'cart',
      payload: {
        cart,
        count: count,
      },
    });
  };

  useEffect(() => {
    emitter.on(eventTypes.cartCountUpdate, ({cart}) => {
      hydrateCart(cart);
    });
  }, []);
  
  return <span>Cart ({state.count})</span>;
};

export default BagCounter;
