import React, { useEffect, useRef, useState } from 'react';
import { clearAllBodyScrollLocks, disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

import Cookies from 'js-cookie';
import cx from 'classnames'

const initialState = {
  cart: {},
  items: [],
  note: '',
  cookieName: 'hausLegalAge',
};

const AgeGate = () => {
  const refEl = useRef(null);
  const [show, setShow] = useState(false);
  const [showMsg, setShowMsg] = useState(false)
  const { cookieName } = initialState;
  const hasCookie = Cookies.get(cookieName)
  
  useEffect(() => {
    if (hasCookie) {
      setShow(false);
      clearAllBodyScrollLocks()
    } else {
      setShow(true);
      disableBodyScroll(refEl)
    }
  }, []);
  
  const setTrueCookie = () => {
    setShow(false);
    Cookies.set(cookieName, 'true', { expires: 30 });
    clearAllBodyScrollLocks();
  }
  
  const setFalseCookie = () => {
    setShowMsg(true)
  }
  
  const Inner = () => (
    <div className={cx("age-consent gutter-px-xs-3 gutter-px-sm-2 -pt-40 -pb-55 bps-pt-55 bps-pb-70 -tac")}>
      <div className={cx('age-consent-primary', {
        'hidden': showMsg
      })}>
        <h3 className="-s-heading -no-mt">Are you 21 or older?</h3>
        <p className="-caption bps-body -no-my">
          You must be of legal drinking age to purchase Haus.
        </p>
        <div className="-mt-30">
          <button
            className="btn btn--white -mx-5 -mb-10"
            onClick={setTrueCookie}
          >
            Yes
          </button>
        </div>
        <button 
          className="text-link text-white -mt-25"
          onClick={setFalseCookie}
        >No, I’m not of drinking age</button>
      </div>
      
      <div className={cx('-s-heading tex-white ros consent-message', {
        'open': showMsg
      })}>You must be of legal drinking age to purchase Haus</div>
    </div>
    )
  
  
  
  return (
    <div ref={refEl} className={cx("age-consent-wrapper",{
      'open': show
    })}>
      <Inner />
    </div>
  );
};

export default AgeGate;